<script>
import tableComponent from '@/utils/vueUtils/tableComponent.vue';
import { productApi } from '@/api/index.js';
import { mixinProduct } from '@/utils/jsUtils/mixinProduct.js';

export default {
	components: {
		tableComponent,
	},
	name: 'ProductListOnReg',
	data() {
		return {
			productList: [],
			tableComponentKey: 0,
		};
	},
	methods: {
		async selectProductList() {
			// mixinProduct.js 에서 처리
			// this.paramVo.prdStatCd = '05';
			let params = this.setProductParam();
			if (!params) return false;

			// let data = {};
			await productApi
				.selectProductListApi(JSON.stringify(this.paramVo))
				.then(response => {
					this.productList = response.data;
				})
				.catch(error => {
					console.log(error);
				});
		},
	},
	mixins: [mixinProduct],
	mounted() {
		this.initProductParam();
		this.paramVo.prdSelCd = '01';
		this.setCtgrList(1);
	},
	created() {},
};
</script>

